import React, { Component } from 'react'
import { Container } from 'react-bootstrap';

class GDPR extends Component {
    render() {
        return (
            <Container>
                <section class="about py-5" id="GDPR">
                <div className="container">
                    <div className="row">
                        <div className="col-4 mx-auto col-md-12 my-5">
                            <h3 className="text-capitalize"><center>Politica de confidențialitate și siguranță privind datele cu caracter personal</center></h3>
                            <br></br>
                            <p className="my-4 text">
                                <br></br>
                                <center><strong>Ce este GDPR?</strong></center>
                                <br></br>
                                GDPR este acronimul pentru General Data Protection Regulation (GDPR) și se referă la Regulamentului Uniunii Europene Nr. 679/2016 privind protecția, confidențialitatea și accesul la datele cu caracter personal ale persoanelor fizice de pe teritoriul Uniunii Europene (UE) și Zonei Economice Europene (ZEE). Acest regulament se aplică începând cu 25 mai 2018 de către toate statele membre ale UE.
                                Fără a reproduce regulamentul împreună cu prevederile acestuia (îl găsiți accesând acest link), vom sintetiza aici răspunsuri la punctele cheie care se aplică în cadrul organizație noastre:
                                <br></br>
                                <br></br>
                                <center><strong>Ce date personale colectează Mano Libre Coffee Roasters și prin ce metode?</strong></center>
                                <br></br>
                                Mano Libre Coffee Roasters colectează exclusiv datele personale necesare încheierii tranzacției pentru care clientul și-a dat consimțământul. Acestea sunt informațiile minime cerute de legislația română în vigoare și sunt necesare facturării de produse și servicii. Adițional, pentru tranzacțiile care implică expedierea de produse, vor fi solicitate strict datele necesare derulării acestui serviciu. Orice date suplimentare oferite, sunt opționale (nu sunt necesare pentru încheierea unei tranzacții) și nu fac obiectul datelor cu caracter personal. Odată cu transmiterea unei comenzi, aceste date pot fi colectate electronic (pe site sau prin email), telefonic sau personal.
                                Mano Libre Coffee Roasters nu colecteză sau procesează alte date cu caracter personal în scopuri de analize de marketing sau reclame.
                                <br></br>
                                <br></br>
                                <center><strong>Cine are acces la aceste date și cum sunt prelucrate? Sunt acestea partajate cu părți terțe?</strong></center>
                                <br></br>
                                Datele cu caracter personal sunt prelucrate de către NovoRoastery SRL (Mano Libre Coffee Roasters), având datele de identificare specificate la secțiunea ‚Contact’ al site-ului Mano Libre Coffee Roasters. La datele colectate și stocate în sistemul software are acces exclusiv personalul Mano Libre Coffee Roasters implicat în operațiunile aferente tranzacțiilor comerciale, respectiv operațiunile de vânzare, facturare, ambalare și expediere. Datele minime necesare expedierii de produse (dacă s-a optat pentru acest serviciu) vor fi partajate cu serviciul de curierat folosit în tranzacția respectivă. Adițional, în concordanță cu legislația în vigoare, Mano Libre Coffee Roasters furnizează datele necesare raportărilor financiare către administrațiile naționale, în regim periodic sau pe bază de solicitare legală.
                                Orice activități secundare desfășurate de Mano Libre Coffee Roasters (ex. campanii de promovare, comunicări, etc.) se bazează pe consimțământul dat explicit pentru aceste scopuri.
                                Mano Libre Coffee Roasters nu partajează datele personale cu nicio altă terță parte și pentru niciun alt scop decât cele enunțate mai sus.
                                <br></br>
                                <br></br>
                                <center><strong>Unde sunt păstrate datele colectate? Sunt acestea în siguranță?</strong></center>
                                <br></br>
                                Pe suport fizic, datele sunt păstrate în arhiva financiară proprie, cu acces exclusiv al personalului financiar. Electronic, datele sunt păstrate pe sisteme securizate pe multiple nivele iar copiile de siguranță sunt criptate în mod automat și păstrate sub această formă. Colectarea datelor prin intermediul site-ul Mano Libre Coffee Roasters se efectuează criptat, pe baza unui certificat valid, reînnoit periodic.
                                <br></br>
                                <br></br>
                                <center><strong>Cum pot să-mi accesez datele cu caracter personal, să le rectific sau să le șterg?</strong></center>
                                <br></br>
                                Datele furnizate prin intermediul site-ului Mano Libre Coffee Roasters pot fi accesate prin autentificare în sistem folosind contul creat în prealabil sau odată cu furnizarea inițială a acestor date sau printr-o solicitare scrisă către YUME. În acest scop, folosiți datele de contact precizate la secțiunea ‚Contact’ al site-ului Mano Libre Coffee Roasters. Mano Libre Coffee Roasters vă va furniza la cerere toate datele proprii cu caracter personal deținute în sistemul informatic, într-un format portabil și ușor accesibil, împreună cu orice alte informații legate de acestea.</p>
                        </div>
                    </div>
                </div>  
                </section>          
        </Container>

        )
    }
}

export default GDPR;